// panel.state: "Choose from: \"Expanded\" - the panel is displayed in full and can be collapsed; \"Collapsed\" - the panel displays only the title and description and can be expanded; \"Locked\" - the panel is displayed in full and cannot be collapsed." => "Odaberite izmeu: \"Proireno\" - ploa je prikazana u cijelosti i moe se saeti; \"Saeto\" - ploa prikazuje samo naslov i opis i moe se proiriti; \"Zakljuano\" - ploa je prikazana u cijelosti i ne moe se saeti."
