// paneldynamic.state: "Choose from: \"Expanded\" - the panel is displayed in full and can be collapsed; \"Collapsed\" - the panel displays only the title and description and can be expanded; \"Locked\" - the panel is displayed in full and cannot be collapsed." => "Odaberite izmeu: \"Proireno\" - ploa je prikazana u cijelosti i moe se saeti; \"Saeto\" - ploa prikazuje samo naslov i opis i moe se proiriti; \"Zakljuano\" - ploa je prikazana u cijelosti i ne moe se saeti."
// paneldynamic.width: "Sets the width of the panel in proportion to other survey elements in the same line. Accepts CSS values (px, %, in, pt, etc.)." => "Postavlja irinu ploe proporcionalno ostalim elementima istraivanja u istoj liniji. Prihvaa CSS vrijednosti (px, %, in, pt itd.)."
// paneldynamic.templateTitle: "Type in a template for dynamic panel titles. Use {panelIndex} for the panel's general position and {visiblePanelIndex} for its order among visible panels. Insert these placeholders into the pattern to add automatic numbering." => "Upiite predloak za dinamike naslove ploa. Koristite {panelIndex} za opi poloaj panela i {visiblePanelIndex} za njegov redoslijed meu vidljivim ploama. Umetnite ova rezervirana mjesta u uzorak da biste dodali automatsko numeriranje."
// paneldynamic.templateTabTitle: "Type in a template for tab titles. Use {panelIndex} for a panel's general position and {visiblePanelIndex} for its order among visible panels. Insert these placeholders into the pattern to add automatic numbering." => "Upiite predloak za naslove kartica. Koristite {panelIndex} za opi poloaj panela i {visiblePanelIndex} za njegov redoslijed meu vidljivim ploama. Umetnite ova rezervirana mjesta u uzorak da biste dodali automatsko numeriranje."
// paneldynamic.templateVisibleIf: "This setting allows you to control the visibility of individual panels within the dynamic panel. Use the `{panel}` placeholder to reference the current panel in your expression." => "Ova postavka omoguuje kontrolu vidljivosti pojedinih ploa unutar dinamike ploe. Koristite rezervirano mjesto '{panel}' za referenciranje trenutne ploe u izrazu."
// paneldynamic.titleLocation: "This setting is automatically inherited by all questions within this panel. If you want to override this setting, define title alignment rules for individual questions. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Ovu postavku automatski nasljeuju sva pitanja unutar ove ploe. Ako elite nadjaati ovu postavku, definirajte pravila poravnanja naslova za pojedinana pitanja. Mogunost \"Naslijedi\" primjenjuje postavku na razini stranice (ako je postavljena) ili na razini upitnika (\"Vrh\" prema zadanim postavkama)."
// paneldynamic.descriptionLocation: "The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Under the panel title\" by default)." => "Opcija \"Naslijedi\" primjenjuje postavku na razini stranice (ako je postavljena) ili na razini upitnika (\"Pod naslovom ploe\" prema zadanim postavkama)."
// paneldynamic.newPanelPosition: "Defines the position of a newly added panel. By default, new panels are added to the end. Select \"Next\" to insert a new panel after the current one." => "Definira poloaj novododane ploe. Prema zadanim postavkama na kraj se dodaju nove ploe. Odaberite \"Dalje\" da biste umetnuli novu plou nakon trenutne."
// paneldynamic.copyDefaultValueFromLastEntry: "Duplicates answers from the last panel and assigns them to the next added dynamic panel." => "Duplicira odgovore s posljednje ploe i dodjeljuje ih sljedeoj dodanoj dinamikoj ploi."
// paneldynamic.keyName: "Reference a question name to require a user to provide a unique response for this question in each panel." => "Pogledajte naziv pitanja kako biste od korisnika zahtijevali da prui jedinstven odgovor za ovo pitanje na svakoj ploi."
// pehelp.defaultValueExpression: "This setting allows you to assign a default answer value based on an expression. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression serves as the initial default value that can be overridden by a respondent's manual input." => "Ova postavka omoguuje dodjeljivanje zadane vrijednosti odgovora na temelju izraza. Izraz moe ukljuivati osnovne izraune - '{q1_id} + {q2_id}', Booleove izraze, kao to su '{age} > 60', i funkcije: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()', itd. Vrijednost odreena ovim izrazom slui kao poetna zadana vrijednost koja se moe nadjaati runim unosom ispitanika."
// pehelp.resetValueIf: "Use the magic wand icon to set a conditional rule that determines when a respondent's input is reset to the value based on the \"Default value expression\" or \"Set value expression\" or to the \"Default answer\" value (if either is set)." => "Pomou ikone arobnog tapia postavite uvjetno pravilo koje odreuje kada se unos ispitanika vraa na vrijednost na temelju \"Zadani izraz vrijednosti\" ili \"Postavi izraz vrijednosti\" ili na vrijednost \"Zadani odgovor\" (ako je postavljena)."
// pehelp.setValueIf: "Use the magic wand icon to set a conditional rule that determines when to run the \"Set value expression\" and dynamically assign the resulting value as a response." => "Pomou ikone arobnog tapia postavite uvjetno pravilo koje odreuje kada pokrenuti \"Postavi izraz vrijednosti\" i dinamiki dodijelite dobivenu vrijednost kao odgovor."
// pehelp.setValueExpression: "Specify an expression that defines the value to be set when the conditions in the \"Set value if\" rule are met. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression can be overridden by a respondent's manual input." => "Navedite izraz koji definira vrijednost koju treba postaviti kada se ispune uvjeti u pravilu \"Postavi vrijednost ako\". Izraz moe ukljuivati osnovne izraune - '{q1_id} + {q2_id}', Booleove izraze, kao to su '{age} > 60', i funkcije: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()', itd. Vrijednost odreena ovim izrazom moe se nadjaati runim unosom ispitanika."
