// question.state: "Choose from: \"Expanded\" - the question box is displayed in full and can be collapsed; \"Collapsed\" - the question box displays only the title and description and can be expanded; \"Locked\" - the question box is displayed in full and cannot be collapsed." => "Odaberite izmeu: \"Proireno\" - okvir pitanja prikazuje se u cijelosti i moe se saeti; \"Saeto\" - okvir pitanja prikazuje samo naslov i opis i moe se proiriti; \"Zakljuano\" - okvir s pitanjima prikazuje se u cijelosti i ne moe se saeti."
// question.titleLocation: "Overrides title alignment rules defined on a panel, page or survey level. The \"Inherit\" option applies any higher-level settings (if set) or survey-level setting (\"Top\" by default)." => "Nadjaava pravila poravnanja naslova definirana na razini ploe, stranice ili upitnika. Opcija \"Naslijedi\" primjenjuje sve postavke vie razine (ako su postavljene) ili postavku na razini upitnika (\"Vrh\" prema zadanim postavkama)."
// question.descriptionLocation: "The \"Inherit\" option applies the survey-level setting (\"Under the question title\" by default)." => "Opcija \"Naslijedi\" primjenjuje postavku na razini upitnika (\"Pod naslovom pitanja\" prema zadanim postavkama)."
// question.errorLocation: "Sets the location of an error message in relation to the question with invalid input. Choose between: \"Top\" - an error text is placed at the top of the question box; \"Bottom\" - an error text is placed at the bottom of the question box. The \"Inherit\" option applies the survey-level setting (\"Top\" by default)." => "Postavlja mjesto poruke o pogreci u odnosu na pitanje s unosom koji nije valjan. Odaberite izmeu: \"Vrh\" - tekst pogreke nalazi se na vrhu okvira pitanja; \"Dno\" - tekst pogreke nalazi se na dnu okvira pitanja. Mogunost \"Naslijedi\" primjenjuje postavku na razini upitnika (\"Vrh\" prema zadanim postavkama)."
// question.indent: "Adds space or margin between the question content and the left border of the question box." => "Dodaje razmak ili marginu izmeu sadraja pitanja i lijevog obruba okvira pitanja."
// question.width: "Sets the width of the question in proportion to other survey elements in the same line. Accepts CSS values (px, %, in, pt, etc.)." => "Postavlja irinu pitanja proporcionalno drugim elementima upitnika u istom retku. Prihvaa CSS vrijednosti (px, %, in, pt itd.)."
// surveyvalidator.expression: "Use the magic wand icon to set a validation rule for the question." => "Pomou ikone arobnog tapia postavite pravilo provjere valjanosti za pitanje."
// question.textUpdateMode: "Choose from: \"On lost focus\" - the value is updated when the input field loses focus; \"While typing\" - the value is updated in real-time, as users are typing. The \"Inherit\" option applies the survey-level setting (\"On lost focus\" by default)." => "Odaberite izmeu: \"Na izgubljenom fokusu\" - vrijednost se aurira kada polje za unos izgubi fokus; \"Tijekom tipkanja\" - vrijednost se aurira u stvarnom vremenu, jer korisnici tipkaju. Opcija \"Naslijedi\" primjenjuje postavku na razini upitnika (\"Pri izgubljenom fokusu\" prema zadanim postavkama)."
// question.url: "You can use any web service as a data source for multiple-choice questions. To populate choice values, enter the URL of the service providing the data." => "Bilo koju web-uslugu moete koristiti kao izvor podataka za pitanja s viestrukim izborom odgovora. Da biste popunili vrijednosti izbora, unesite URL usluge koja prua podatke."
