// file.imageWidth: "Adjusts the width of the image in the survey results." => "Podeava irinu slike u rezultatima upitnika."
// imagepicker.imageHeight: "Overrides the minimum and maximum height values." => "Nadjaava vrijednosti minimalne i maksimalne visine."
