// imagepicker.choices: "\"Value\" serves as an item ID used in conditional rules; \"Text\" is displayed to respondents." => "\"Vrijednost\" slui kao ID stavke koji se koristi u uvjetnim pravilima; \"Tekst\" se prikazuje ispitanicima."
// text.size: "This setting only resizes the input field and doesn't affect the width of the question box. To limit the accepted input length, go to <b>Validation  Maximum character limit</b>." => "Ova postavka samo mijenja veliinu polja unosa i ne utjee na irinu okvira pitanja. Da biste ograniili prihvaenu duljinu unosa, idite na <b>Provjera valjanosti  Maksimalno ogranienje znaka</b>."
