// matrixdropdowncolumn.requiredIf: "Use the magic wand icon to set a conditional rule that prevents survey submission unless at least one nested question has an answer." => "Pomou ikone arobnog tapia postavite uvjetno pravilo koje sprjeava slanje upitnika, osim ako barem jedno ugnijeeno pitanje nema odgovor."
