// pehelp.widthMode: "Choose from: \"Static\" - sets a fixed width; \"Responsive\" - makes the survey occupy the full width of the screen; \"Auto\" - applies either of the two depending on the question types used." => "Odaberite izmeu: \"Statika\" - postavlja fiksnu irinu; \"Responsive\" - ini da anketa zauzima punu irinu zaslona; \"Auto\" - primjenjuje se bilo koji od dva ovisno o koritenim vrstama pitanja."
// pehelp.logo: "Paste an image link (no size limits) or click the folder icon to browse a file from your computer (up to 64KB)." => "Zalijepite slikovnu vezu (bez ogranienja veliine) ili kliknite ikonu mape da biste pregledali datoteku s raunala (do 64 KB)."
