// pehelp.logoFit: "Choose from: \"None\" - image maintains its original size; \"Contain\" - image is resized to fit while maintaining its aspect ratio; \"Cover\" - image fills the entire box while maintaining its aspect ratio; \"Fill\" - image is stretched to fill the box without maintaining its aspect ratio." => "Odaberite izmeu: \"Nema\" - slika zadrava svoju izvornu veliinu; \"Sadri\" - slika se mijenja kako bi stala uz zadravanje omjera slike; \"Poklopac\" - slika ispunjava cijelu kutiju zadravajui omjer slike; \"Ispuna\" - slika se rastee kako bi ispunila kutiju bez zadravanja omjera slike."
