// pehelp.questionTitleLocation: "Applies to all questions within the survey. This setting can be overridden by title alignment rules at lower levels: panel, page, or question. A lower-level setting will override those on a higher level." => "Odnosi se na sva pitanja unutar ankete. Ova postavka moe se nadjaati pravilima poravnanja naslova na niim razinama: ploa, stranica ili pitanje. Postavka nie razine nadjaat e one na vioj razini."
// pehelp.requiredMark: "A symbol or a sequence of symbols indicating that an answer is required." => "Simbol ili niz simbola koji oznaavaju da je potreban odgovor."
