// pehelp.detailPanelMode: "Sets the location of a details section in relation to a row. Choose from: \"None\" - no expansion is added; \"Under the row\" - a row expansion is placed under each row of the matrix; \"Under the row, display one row expansion only\" - an expansion is displayed under a single row only, the remaining row expansions are collapsed." => "Postavlja mjesto sekcije s detaljima u odnosu na redak. Odaberite izmeu: \"Nema\" - ne dodaje se proirenje; \"Ispod reda\" - proirenje retka nalazi se ispod svakog retka matrice; \"Ispod retka prikai samo proirenje jednog retka\" - proirenje se prikazuje samo ispod jednog retka, preostala proirenja retka su saeta."
// pehelp.imageFit: "Choose from: \"None\" - image maintains its original size; \"Contain\" - image is resized to fit while maintaining its aspect ratio; \"Cover\" - image fills the entire box while maintaining its aspect ratio; \"Fill\" - image is stretched to fill the box without maintaining its aspect ratio." => "Odaberite izmeu: \"Nema\" - slika zadrava svoju izvornu veliinu; \"Sadri\" - slika se mijenja kako bi stala uz zadravanje omjera slike; \"Poklopac\" - slika ispunjava cijelu kutiju zadravajui omjer slike; \"Ispuna\" - slika se rastee kako bi ispunila kutiju bez zadravanja omjera slike."
// pehelp.autoGrow: "Gradually increases the height of the input field as data is being entered. Overrides the \"Input field height (in lines)\" setting." => "Postupno poveava visinu polja unosa tijekom unosa podataka. Nadjaava postavku \"Visina polja unosa (u crtama)\"."
