// panel.gridLayoutColumns: "This table lets you configure each grid column within the panel. It automatically sets the width percentage for each column based on the maximum number of elements in a row. To customize the grid layout, manually adjust these values and define the title width for all questions in each column." => "Ova tablica omoguuje konfiguriranje svakog stupca reetke unutar ploe. Automatski postavlja postotak irine za svaki stupac na temelju maksimalnog broja elemenata u redu. Da biste prilagodili izgled reetke, runo prilagodite te vrijednosti i definirajte irinu naslova za sva pitanja u svakom stupcu."
// pehelp.gridLayoutEnabled: "Survey Creator allows you to manually adjust the inline widths of form elements to control the layout. If this doesn't produce the desired outcome, you can enable the grid layout, which structures form elements using a column-based system. To configure layout columns, select a page or panel and use the \"Question Settings\"  \"Grid columns\" table. To adjust how many columns a question spans, select it and set the desired value in the \"Layout\"  \"Column span\" field." => "Survey Creator omoguuje vam runo podeavanje ugraenih irina elemenata obrasca za kontrolu izgleda. Ako to ne daje eljeni ishod, moete omoguiti raspored reetke, ije strukture tvore elemente pomou sustava temeljenog na stupcima. Da biste konfigurirali stupce izgleda, odaberite stranicu ili plou i upotrijebite tablicu \"Postavke pitanja\"  \"Stupci reetke\". Da biste prilagodili koliko stupaca obuhvaa pitanje, odaberite ga i postavite eljenu vrijednost u polju \"Izgled\"  \"Raspon stupaca\"."
// question.effectiveColSpan: "Specifies how many columns this question spans within the grid layout." => "Odreuje koliko stupaca ovo pitanje obuhvaa unutar rasporeda reetke."
// page.gridLayoutColumns: "This table lets you configure each grid column on the page. It automatically sets the width percentage for each column based on the maximum number of elements in a row. To customize the grid layout, manually adjust these values and define the title width for all questions in each column." => "Ova tablica omoguuje konfiguriranje svakog stupca reetke na stranici. Automatski postavlja postotak irine za svaki stupac na temelju maksimalnog broja elemenata u redu. Da biste prilagodili izgled reetke, runo prilagodite te vrijednosti i definirajte irinu naslova za sva pitanja u svakom stupcu."

